.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
  }
  
  .modal {
    max-width: calc(100vw - 48px);
    max-height: calc(100vh - 24px);
  }

  .closeButton{
    width: 30px;
    height: 30px;
    border-radius: 10px;
    background-color: dimgray;
    border: 1px dashed;
    position: absolute;
    top: 70px;
    right: 250px;
    cursor: pointer;
  }

  .closeButton:hover{
    scale: 1.1;
    background-color: beige;
  }